* {
  padding: 0%;
  margin: 0%;
  font-family: 'Rubik', sans-serif;
  box-sizing: border-box;
}

@import url('https://fonts.googleapis.com/css2?family=Rubik&display=swap');

.mianDiv {
  align-items: center;
  justify-content: center;
  display: flex;
  background-color: #83c5be;
  width: 100vw;
  height: 100vh;
}

.innerDiv {
  /* display: flex; */
  background-color: #007c88;
  width: 300px;
  height: 500px;
  border-radius: 20px;
}

.header {
  margin: 20px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.plusIcn {
  position: relative;
  font-size: 25px;
  color: rgb(0, 0, 0);
  margin-top: 12px;
  cursor: pointer;

}

input::placeholder {
  opacity: .4;
  color: aliceblue;
  letter-spacing: 2px;
  text-align: center;
}

.innerDiv input {
  outline: none;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 2px solid black;
  height: 35px;
  background-color: transparent;
  width: 70%;
}


.listsDiv {
  /* background-color: #2c3f42; */
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.lists {
  list-style: none;
  /* background: #000; */
  /* width: 85%; */
  margin: 10px;
  /* background-color: #dfdfdf; */
  /* margin-left: 20px; */
}

.lists li {
  padding-left: 20px;
  color: aliceblue;
  text-align: start;
  /* background-color: #718f8c; */
  height: 25px;
  /* background-color: aquamarine; */

}

.lists li i {
  margin-right: 5px;
  color: rgb(0, 0, 0);
}

.lists:hover i {
  transform: .1s ease;
  color: rgb(225, 0, 0);
}